.payment-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  margin: 0;

  /* Image de fond */
  background-image: url('/public/rouge.avif');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  color: #dc9c06;
}

.payment-form {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  width: 100%;
  max-width: 400px;
}

.payment-form h2 {
  font-size: 1.8em;
  margin-bottom: 15px;
  text-align: center;
  color: #333;
}

.form-group {
  margin: 15px 0;
}

.payment-form input,
.payment-form button {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.payment-form button {
  background-color: #821d1b;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.payment-form button:hover {
  background-color: #3d0e0d;
  transform: scale(1.05);
}
