

/* Navigation */
.navbar {
    position: absolute;
    top: 80px; /* Ajuster la position de la navbar sous la ligne blanche */
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: space-between; /* Diviser la nav en 2 parties */
    width: 70%;
    z-index: 20;
    border-top: 1px solid white; /* Ligne blanche fine en haut de la navbar */
    border-bottom: 1px solid white; /* Ligne blanche fine en bas de la navbar */
    padding: 20px 20px;
}

/* Diviser la navigation en 2 parties */
.navbar-left,
.navbar-right {
    display: flex;
    gap: 30px;
}

.navbar-center {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    font-size: 20px;
    font-weight: bold;
    color: white;
    letter-spacing: 2px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7);
}

/* Drapeau tunisien */
.navbar-center img {
    width: 20px;
    height: 40px;
    border-radius: 50%;
}

/* Liens de la navigation */
.navbar ul {
    list-style: none;
    display: flex;
    gap: 30px;
    padding: 0;
    margin: 0;
}

.navbar ul li {
    display: inline;
}

.navbar ul li a {
    text-decoration: none;
    color: rgb(251, 251, 251);
    font-size: 15px;
    font-weight: bold;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    padding: 5px 10px;
    transition: all 0.3s ease; /* Effet de transition pour la couleur et le cadre */
}

.navbar ul li a:hover {
    color: #641513; /* Changer la couleur au survol */
    background-color: rgba(255, 255, 255, 0.2); /* Ajouter un fond léger au survol */
    border-radius: 5px; /* Bord arrondi pour l'effet du cadre */
    border: 2px solid #641513; /* Bordure autour de l'élément */
}
