/* Style pour le conteneur vidéo */
#video-container {
    position: relative; /* Pour positionner le bouton Skip par rapport à la vidéo */
    width: 100vw; /* Largeur de la vidéo = largeur de la fenêtre */
    height: 100vh; /* Hauteur de la vidéo = hauteur de la fenêtre */
    overflow: hidden; /* Empêcher tout dépassement */
  }
  
  /* Style pour la vidéo */
  #intro-video {
    width: 100%; /* Occupe toute la largeur de l'écran */
    height: 100%; /* Occupe toute la hauteur de l'écran */
    object-fit: cover; /* Couvrir toute la fenêtre sans déformation */
    z-index: 1; /* S'assurer que la vidéo est derrière le bouton */
  }
  
 
  /* Effet au survol du bouton */
 
  
  /* Pour empêcher les marges et paddings par défaut du navigateur */
  body, html {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
  /* Empêche le défilement */
  }
 