/* Modale principale */
/* Vidéo en arrière-plan */
.background-video video {
    transition: opacity 0.5s ease-in-out;
     /* Légère transparence pour une meilleure esthétique */
  }
 /* Modale centrée avec plein écran */
.modaldiscover {
    position: fixed;
    top: 0%;
    left: 0%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8); /* Fond sombre avec opacité */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    padding: 0;
    overflow: hidden;
  }
  
  /* Vidéo occupe tout l'écran */
  .video-container {
    position: relative;
    width: 80%;
    height: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
  }
  
  .video-container video {
    width: 100%;
    height: 100%;
    object-fit: cover; /* Remplir l'écran tout en conservant l'aspect */
    transition: transform 0.3s ease;
    border-radius: 15px;
  }
  
  
  
  /* Boutons de navigation (précédent et suivant) */
  .video-controls {
    position: absolute;
    top: 50%;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 20px;
    transform: translateY(-50%);
    z-index: 10;
    color: #641513;
  }
  
  .control-btn {
    background: #641513; /* Fond semi-transparent */
    color: white;
    font-size: 20px;
    border: none;
    padding: 15px;
    border-radius: 50%;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.3s ease;
    opacity: 0.7;
  }
  
  .control-btn:hover {
    background: #b51a17(0, 0, 0, 0.8);
    transform: scale(1.1); /* Agrandir les boutons au survol */
    opacity: 1;
  }
  
  /* Bouton de fermeture */
  .close-modaldiscover {
    position: absolute;
    top: 20px;
    right: 20px;
    background: #641513;
    color: white;
    border: none;
    padding: 10px;
    font-size: 20px;
    border-radius: 50%;
    cursor: pointer;
    opacity: 0.7;
    transition: background 0.3s ease, opacity 0.3s ease;
  }
  
  .close-modaldiscover:hover {
    background: rgba(0, 0, 0, 0.8);
    opacity: 1;
  }
  
  /* Animation pour l'apparition de la modale */
  @keyframes modalFadeIn {
    0% { opacity: 0; transform: scale(0.9); }
    100% { opacity: 1; transform: scale(1); }
  }
  