.guides-page {
    padding: 40px;
    font-family: 'Arial', sans-serif;
    background-color: #f9f9f9;
    background-image: url('/public/rouge.avif'); /* Remplacez par le chemin de votre image */
    background-size: cover; /* L'image couvre toute la page */
    background-repeat: no-repeat; /* Pas de répétition */
    background-position: center; /* Centre l'image */
    color: #333;
  }
  
  .guides-page h1 {
    text-align: center;
    margin-bottom: 20px;
    font-size: 2.5em;
    color: #ffffff;
  }
  
  .guides-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    padding: 0;
  }
  
  .guides-list li {
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 15px;
    margin: 10px;
    width: 400px;
    
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .guides-list li:hover {
    transform: scale(1.05);
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.2);
  }
  
  .guides-list h2 {
    font-size: 1.5em;
    margin-bottom: 10px;
    color: #821d1b;
  }
  
  .guides-list p {
    font-size: 1em;
    color: #555;
  }
  
  .login-guide {
    margin-top: 30px;
    text-align: center;
  }
  
  .login-guide-button {
    background-color: #ffffff;
    color: #821d1b;
    border: none;
    padding: 10px 20px;
    font-size: 1.2em;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.4s ease;
  }
  
  .login-guide-button:hover {
    background-color: #ffffff;
    transform: scale(1);
  }
  .guide-image {
    width: 150px; /* Largeur fixe */
    height: 150px; /* Hauteur fixe */
    object-fit: cover;
    border-radius: 50%; /* Transforme l'image en cercle */
    margin: 0 auto 10px; /* Centre l'image horizontalement */
  }
  .search-bar {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .search-input {
    width: 30%;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ddd;
    border-radius: 50px;
    box-shadow: 0px 2px 4px #3d3c3c;
    transition: box-shadow 0.3s ease;
  }
  
  .search-input:focus {
    outline: none;
    box-shadow: 0px 4px 8px #e0e0e0;
  }
  