.signup-page {
    padding: 40px;
    font-family: 'Arial', sans-serif;
    color: #ffffff;
    text-align: center;
  
    /* Image de fond */
    background-image: url('/public/rouge.avif'); /* Remplacez par le chemin de votre image */
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  
    /* S'assure que la page occupe toute la hauteur */
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    
  }
  .signup-page h1{
    font-size: 80px;
  }
  
  .signup-form {
    max-width: 500px;
    margin: 0 auto;
    background: rgba(255, 255, 255, 0.9); /* Fond blanc avec transparence */
    padding: 40px;
    border-radius: 8px;
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
  }
  
  .signup-form input,
  .signup-form textarea {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .signup-form textarea {
    height: 100px;
  }
  
  .signup-button {
    background-color: #821d1b;
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 1em;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease, transform 0.2s ease;
  }
  
  .signup-button:hover {
    background-color: #3d0e0d;
    transform: scale(1.05);
  }
  