/* BlogPage.css */

/* --- Variables CSS pour un Thème Dynamique --- */
:root {
  --primary-color: #1e90ff; /* Bleu vif */
  --secondary-color: #32cd32; /* Vert professionnel */
  --background-color: #eef2f3; /* Fond clair */
  --text-color: #333333; /* Texte lisible */
  --hover-bg: #104e8b; /* Effet sur les boutons */
  --comment-bg: #f0f8ff; /* Dégradé léger pour les commentaires */
  --input-border: #b0bec5;
  --box-shadow: rgba(0, 0, 0, 0.2);
}

/* --- Réinitialiser les valeurs par défaut pour éviter conflits --- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

/* --- Style Global pour la Section Blog uniquement --- */
.container {
  max-width: 1100px;
  margin: 20px auto;
  padding: 20px;
  background: var(--background-color);
  border-radius: 8px;
  box-shadow: 0 4px 10px var(--box-shadow);
}

/* Titre Principal de la Page */
.container h1 {
  font-size: 2.5rem;
  color: var(--primary-color);
  text-align: center;
  margin-bottom: 20px;
}

/* Liste d'articles dynamiques */
.article-link-btn {
  background: var(--primary-color);
  color: white;
  font-size: 1.1rem;
  border: none;
  padding: 12px 25px;
  margin: 10px;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s ease, transform 0.2s ease;
}

.article-link-btn:hover {
  background: var(--hover-bg);
  transform: scale(1.05);
}

/* Retour vers la liste d'articles */
.back-btn {
  background-color: var(--secondary-color);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.back-btn:hover {
  background-color: #28a745;
}

/* Conteneur des Articles Dynamique */
.article-container {
  padding: 20px;
  border: 1px solid var(--input-border);
  border-radius: 8px;
  background: white;
  box-shadow: 0 2px 5px var(--box-shadow);
  margin: 15px 0;
  transition: transform 0.2s ease;
}

.article-container:hover {
  transform: translateY(-3px);
}

.article-container h1 {
  font-size: 1.7rem;
  margin-bottom: 10px;
  color: var(--text-color);
}

.article-container p {
  font-size: 1rem;
  color: var(--text-color);
  margin: 10px 0;
}

.article-container a {
  color: var(--secondary-color);
  text-decoration: none;
  transition: color 0.2s ease;
}

.article-container a:hover {
  text-decoration: underline;
}

/* Section des commentaires */
.comments-section {
  background: var(--comment-bg);
  padding: 15px;
  border-radius: 8px;
  margin: 15px 0;
  box-shadow: 0 2px 4px var(--box-shadow);
}

.comments-section h2 {
  color: var(--primary-color);
  font-size: 1.4rem;
  margin-bottom: 10px;
}

.comment-input {
  width: 100%;
  border: 1px solid var(--input-border);
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  transition: border-color 0.3s ease;
}

.comment-input:focus {
  border-color: var(--primary-color);
}

.add-comment-btn {
  background-color: var(--primary-color);
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.add-comment-btn:hover {
  background-color: var(--hover-bg);
}

/* Responsivité */
@media (max-width: 768px) {
  h1 {
    font-size: 2rem;
  }

  .article-link-btn,
  .back-btn {
    font-size: 0.95rem;
  }

  .article-container {
    padding: 15px;
  }
}

@media (max-width: 480px) {
  h1 {
    font-size: 1.7rem;
  }

  .article-link-btn,
  .back-btn,
  .add-comment-btn {
    width: 100%;
  }
}






  