/* Style pour le conteneur du dropdown */
.dropdown {
  position: absolute;
  top: 30px;
  left: 0;
  background-color: rgba(255, 255, 255, 0.5); /* Fond semi-transparent */
  border-radius: 8px; /* Bordure légèrement arrondie */
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* Ombre douce */
  padding: 1px; /* Espacement interne */
  z-index: 100;
  width: 300px; /* Largeur ajustée pour un affichage vertical */
}

/* Liste des éléments dans le dropdown */
.dropdown ul {
  
  margin: 0; /* Pas de marges */
  padding: 0; /* Pas de padding */
  display: flex;
  flex-direction: column; /* Aligner les éléments verticalement */
  gap: 10px; /* Espacement vertical entre les éléments */
}

/* Style des éléments individuels */
.dropdown ul li {
  padding: 8px 12px; /* Espacement interne */
  cursor: pointer; /* Indiquer qu'ils sont cliquables */
  color: #641513; /* Couleur de texte */
  font-weight: bold; /* Texte en gras */
   /* Alignement à gauche */
  font-size: 14px; /* Taille de police */
 
  border-radius: 6px; /* Coins arrondis */
  /* Animation fluide */
}

/* Effet au survol */
.dropdown ul li:hover {
  background-color: rgba(251, 238, 238, 0.621); /* Fond de survol */
  /* Agrandissement léger */
}



