/* Style du tableau dans le modal */
.modal-content table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
    border: 2px solid #ccc;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    background-color: #fff;
  }
  
  /* Style des en-têtes du tableau */
  .modal-content th {
    background-color:#641513; /* Couleur de fond rouge */
    color: #fff;
    padding: 12px;
    text-align: left;
    font-size: 18px;
    border: 1px solid #ddd;
    border-bottom: 2px solid #ddd;
  }
  
  /* Style des cellules du tableau */
  .modal-content td {
    padding: 12px;
    text-align: left;
    font-size: 16px;
    border: 1px solid #ddd;
  }
  
  /* Style des lignes du tableau (alternance de couleurs) */
  .modal-content tr:nth-child(even) {
    background-color: #f9f9f9;
  }
  
  .modal-content tr:nth-child(odd) {
    background-color: #ffffff;
  }
  
  /* Style du tableau au survol */
  .modal-content tr:hover {
    background-color: #ffefef;
    cursor: pointer;
  }
  
  /* Style du tableau pour les petits écrans */
  @media screen and (max-width: 768px) {
    .modal-content table {
      font-size: 14px;
    }
  
    .modal-content th, .modal-content td {
      padding: 8px;
    }
  }
  /* Style de la modal */
  .modal {
    position: fixed; /* Fixé par rapport à la fenêtre */
    top:520%; /* Centré verticalement */
    left: 50%; /* Centré horizontalement */
    transform: translate(-50%, -50%); /* Ajustement pour centrer exactement */
    width: 90%; /* Largeur adaptative */
    max-width: 700px; /* Largeur maximale */
    max-height: 80vh; /* Limiter la hauteur pour ne pas dépasser l'écran */
    overflow: hidden; /* Empêcher le contenu de déborder */
    background-color: white; /* Fond blanc */
    padding: 30px; /* Espacement interne */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Ombre douce */
    z-index: 1000; /* Priorité d'affichage élevée */
    border-radius: 8px; /* Bords arrondis */
    display: flex; /* Flexbox pour alignement interne si nécessaire */
    flex-direction: column; /* Colonne pour les enfants */
    justify-content: center; /* Centre le contenu verticalement */
    align-items: center; /* Centre le contenu horizontalement */
}

  .modal-content {
    max-height: 80vh; /* Permet le défilement uniquement du contenu */
    overflow-y: auto;
  }
  
  .modal-close {
    position: absolute;
    top: 20px;
    right: 10px;
    font-size: 24px;
    font-weight: bold;
    cursor: pointer;
    z-index: 1001; /* Toujours au-dessus du contenu */
    background: none;
    border: none;
    color: black;
  }
  
  /* Animation pour faire apparaître la modal */
  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  /* Animation de la modal pour la faire glisser du bas */
  @keyframes slideUp {
    from {
      transform: translateY(50px);
    }
    to {
      transform: translateY(0);
    }
  }
  
  
 
  /* Style pour les titres */
  h3 {
    font-family: 'Roboto', sans-serif;
    color: #641513;
    text-align: center;
    font-size: 28px;
    margin-bottom: 15px;
    font-style: italic;
    
  }
  
  /* Style pour le texte du paragraphe */
  p {
    font-family: 'Arial', sans-serif;
    color: #000000;
    font-size: 17px;
    line-height: 1.6;
    text-align: justify;
    text-justify: inter-word;
  }
  
  /* Ajouter une bordure et ombre subtile autour du texte pour un effet élégant */
  .modal-content p {
    border-left: 5px solid #641513;
    padding-left: 15px;
    margin-top: 20px;
    margin-bottom: 10px;
  }
  /* Style pour le titre principal */
.modal-title {
    font-size: 24px;
    color: #641513;
    margin-bottom: 15px;
    text-align: center;
    font-style: italic;
  }
  
  /* Style pour le texte en gras */
  .modal-bold {
    font-weight: bold;
    color: #2f100c;
  }
  
  /* Style pour les sous-titres */
  .modal-subtitle {
    font-size: 20px;
    color: #821d1b;
    margin-top: 20px;
    margin-bottom: 10px;
    text-align: left;
  }
  
 
  
  
  /* Style général pour le texte */
  .modal-content p {
    font-size: 16px;
    line-height: 1.6;
    color: #000000;
    margin-bottom: 15px;
    text-align: justify;
  }
  
  /* Ajouter de l'espace autour des sections */
  .modal-content {
    padding: 20px;
  }
  
  /* Style pour les marges internes */
  .modal-content p, .modal-content h4 {
    margin-left: 10px;
    margin-right: 10px;
  }
 
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: scale(0.95);
    }
    to {
      opacity: 1;
      transform: scale(1);
    }
  }
  
  .modal-exit {
    animation: fadeOut 0.3s ease-in-out;
  }
  
  @keyframes fadeOut {
    from {
      opacity: 1;
      transform: scale(1);
    }
    to {
      opacity: 0;
      transform: scale(0.95);
    }
  }
