/* marsa.css */








/* Conteneur de description */
.marsa-description {
  display: flex;
  align-items: center;
  gap: 30px;
  max-width: 1100px;
  margin: 50px auto;
  padding: 30px;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 10px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
}

.marsa-description img {
  width: 600px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.marsa-description p {
  font-size: 20px;
  line-height: 1.8;
  color: #555;
  text-align: justify;
}

/* Section d'images */
.marsa-images {
  text-align: center;
  margin: 50px 20px;
}

.marsa-images h2 {
  font-size: 32px;
  margin-bottom: 20px;
  color: #541614;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.3);
}

.images-grid {
  display: flex;
  justify-content: center;
  gap: 30px;
  flex-wrap: wrap;
  margin-top: 20px;
}

.images-grid img {
  width: 400px;
  height: 300px;
  object-fit: cover;
  border-radius: 10px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.images-grid img:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.3);
}

